import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class DetailsAdminService {

  server_address:string='/api';

  constructor(private http:HttpClient) { }

  getDonorsReq(){
    return this.http.get(`${this.server_address}/admin/get_donors_request`);
  }

  acceptDonor(donor:any){
    console.log(donor);
    return this.http.put(`${this.server_address}/admin/accept_donors_request`,donor)
    .subscribe(data=>{console.log(data)});
  }

  rejectDonor(donor:any){
    console.log(donor);
    return this.http.delete(`${this.server_address}/admin/reject_donors_request/`+donor)
    .subscribe(data=>{console.log(data)});
  }

  getDonors(){
    return this.http.get(`${this.server_address}/admin/get_donors`);
  }

  getDonor(id:any){
    return this.http.get(`${this.server_address}/admin/get_donor/`+id);
  }

  editDonor(donor:any){
    console.log(donor);
    return this.http.put(`${this.server_address}/admin/edit_donor`,{'donor':donor})
    .subscribe(data=>{console.log(data)});
  }

  deleteDonor(id:any){
    console.log(id);
    return this.http.delete(`${this.server_address}/admin/delete_donor/`+id)
    .subscribe(data=>{console.log(data)});
  }
}
