import { HttpClient } from '@angular/common/http';
import { ValueConverter } from '@angular/compiler/src/render3/view/template';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class DetailsService {

  server_address:string='/api';

  constructor(private http:HttpClient) { }

  getUser(email:any){
    return this.http.get(`${this.server_address}/user/details/`+email);
  }

  getDonors(){
    return this.http.get(`${this.server_address}/user/get_donors`);
  }

  getDonor(email:any){
    return this.http.get(`${this.server_address}/user/get_donor/`+email);
  }

  addDonor(donor:any){
    console.log(donor);
    return this.http.post(`${this.server_address}/user/add_donor`,{'donor':donor})
    .subscribe(data=>{console.log(data)});
  }

  editDonor(donor:any){
    console.log(donor);
    return this.http.put(`${this.server_address}/user/edit_donor`,{'donor':donor})
    .subscribe(data=>{console.log(data)});
  }

  deleteDonor(email:any){
    console.log(email);
    return this.http.delete(`${this.server_address}/user/delete_donor/`+email)
    .subscribe(data=>{console.log(data)});
  }
}
